___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: rgb(73, 73, 73);
  color: white;
}

.app {
  width: 100%;
}

.header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  margin: 20px 40px 30px;
  width: calc(100vw - 80px);
}

.header img {
  display: flex;
  align-items: center;
  height: 40px;
}

.header .search-container {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  margin: 0 20px;
}

.search-container input {
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 20px;
  width: 200px;
  text-align: center;
}

.unit-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.unit-buttons button {
  padding: 15px;
  font-size: 12px;
  border: none;
  background: #404040;
  color: #f7f7f7;
  border-radius: 10px;
  font-weight: bold;
}

.unit-buttons button:hover {
  background: #303030;
}
.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.current-weather {
  display: flex;
  text-align: right;
  flex-direction: row-reverse;
  align-items: center;
  gap: 5px;
  margin-right: 40%;
  font-size: 1.5vw;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.current-weather-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  width: 100%;
  height: 250px;
  align-items: center;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.location-info {
  text-align: left;
  margin-left: 40%;
  font-size: 1.5vw;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.current-weather img {
  width: 100px;
  height: auto;
}

.current-weather-info {
  grid-area: 1 / 2 / 2 / 3;
  text-align: right;
}

.current-weather-info p {
  margin: 5px 0;
}

.more-weather-info {
  grid-area: 2 / 1 / 3 / 3;
  width: 60%;
}

.weekly-forecast-container {
  text-align: center;
  width: 60%;
}

.weekly-forecast {
  padding: 20px;
}

.weekly-forecast h2 {
  text-align: center;
}

.day {
  padding: 10px 5px 10px;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  align-items: center;
  text-align: center;
  border-bottom: white solid 1px;
}

.weekly-forecast .day p {
  margin: 0;
}

.weekly-forecast .day img {
  height: 40px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: auto;
}

.more-weather-info-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin-top: 20px;
}

.grid-item {
  background: #444;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

.grid-item p {
  margin: 5px 0;
}

.footer {
  padding: 10px;
  
  color: white;
  text-align: center;
  
  bottom: 0;
  left: 0;
}

@media (max-width: 768px) {
  .header {
    grid-template-columns: 1fr;
    margin: 10px 20px;
    width: auto;
    text-align: center;
  }

  .header img {
    justify-self: center;
    margin-bottom: 10px;
  }

  .header .search-container {
    margin: 10px 0;
  }

  .unit-buttons {
    justify-content: center;
  }

  .current-weather-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
  }

  .current-weather-info {
    text-align: center;

  }

  .current-weather {
    text-align: center;
    margin-right: 0%;
    font-size: 3vw;
  }

  .location-info {
    text-align: center;
    margin-left: 0%;
    font-size: 3vw;
  }

  .weekly-forecast-container {
    width: 90%;
  }

  .day {
    text-align: left;
    padding: 10px 0;
  }

  .day p, .day img {
    text-align: center;
    margin: 5px 0;
  }

  .more-weather-info {
    width: auto;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.css"],"names":[],"mappings":"AAAA;EACE,8BAA8B;EAC9B,SAAS;EACT,UAAU;EACV,2BAA2B;EAC3B,YAAY;AACd;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,aAAa;EACb,kCAAkC;EAClC,mBAAmB;EACnB,sBAAsB;EACtB,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,YAAY;EACZ,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,YAAY;EACZ,mBAAmB;EACnB,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,eAAe;EACf,YAAY;EACZ,mBAAmB;EACnB,cAAc;EACd,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,2BAA2B;EAC3B,mBAAmB;EACnB,QAAQ;EACR,iBAAiB;EACjB,gBAAgB;EAChB,2CAA2C;AAC7C;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,kCAAkC;EAClC,oBAAoB;EACpB,iBAAiB;EACjB,WAAW;EACX,aAAa;EACb,mBAAmB;EACnB,6CAA6C;AAC/C;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;EAChB,2CAA2C;AAC7C;;AAEA;EACE,YAAY;EACZ,YAAY;AACd;;AAEA;EACE,wBAAwB;EACxB,iBAAiB;AACnB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,wBAAwB;EACxB,UAAU;AACZ;;AAEA;EACE,kBAAkB;EAClB,UAAU;AACZ;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,sBAAsB;EACtB,aAAa;EACb,sCAAsC;EACtC,mBAAmB;EACnB,kBAAkB;EAClB,8BAA8B;AAChC;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,YAAY;EACZ,cAAc;EACd,iBAAiB;EACjB,kBAAkB;EAClB,WAAW;AACb;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,cAAc;EACd,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;;EAEb,YAAY;EACZ,kBAAkB;;EAElB,SAAS;EACT,OAAO;AACT;;AAEA;EACE;IACE,0BAA0B;IAC1B,iBAAiB;IACjB,WAAW;IACX,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;IACpB,mBAAmB;EACrB;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,uBAAuB;EACzB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;EACX;;EAEA;IACE,kBAAkB;;EAEpB;;EAEA;IACE,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;EAChB;;EAEA;IACE,kBAAkB;IAClB,eAAe;IACf,cAAc;EAChB;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,gBAAgB;IAChB,eAAe;EACjB;;EAEA;IACE,kBAAkB;IAClB,aAAa;EACf;;EAEA;IACE,WAAW;EACb;AACF","sourcesContent":["body {\n  font-family: Arial, sans-seri